	module.exports = {"data":{"markdownRemark":{"html":"<p>In this session we have talks lined up from Mike Doyle and Jason Lante. </p>\n<p>Huge thanks to Chris Rae and the good folks at D'alRae Solutions for hosting us.  </p>\n<h1 id=\"session-overview\"><a href=\"#session-overview\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Session Overview</h1>\n<h2 id=\"session-1-what-abap--ui5-devs-need-to-know-about-the-sap-cloud-platform-sdk-for-ios\"><a href=\"#session-1-what-abap--ui5-devs-need-to-know-about-the-sap-cloud-platform-sdk-for-ios\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Session 1: What ABAP &#x26; UI5 devs need to know about the SAP Cloud Platform SDK for iOS</h2>\n<h3 id=\"-mike-doyle-\"><a href=\"#-mike-doyle-\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>( Mike Doyle )</h3>\n<p>The SAP Cloud Platform SDK for iOS helps us to build native iOS apps that connect seamlessly to back-end systems like S/4HANA and ECC. Mike will\ntalk us though what we need to know to help us get started.  For more details you should also check out his <a href=\"https://blogs.sap.com/2018/08/09/sdk-for-ios-your-quick-start-guide/\">SAP Community blog post</a> on the topic</p>\n<h2 id=\"session-2-modern-abap-development-techniques\"><a href=\"#session-2-modern-abap-development-techniques\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Session 2: Modern ABAP Development Techniques</h2>\n<h3 id=\"-jason-lante-\"><a href=\"#-jason-lante-\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>( Jason Lante )</h3>\n<p>Since 7.40, ABAP has been significantly improved to make your code more efficient. Jason's talk will help you learn how to write simplified ABAP code leveraging techniques from other programming languages.</p>\n<p>You can access the teched content \"DEV262 – Evolution of the ABAP Programming Language - Karsten Bohlmann, Holger Janz &#x26; Thomas Jung\" that Jason references in his talk <a href=\"https://sitbne.github.io/media/2018-09-05/E3_Presentation_ Evolution of the ABAP Programming Language\" download>here</a></p>\n<p>Another excellent reference are Horst Keller's blog posts with the Language News for each ABAP release.  Jason refers specifically to this classic post for the <a href=\"https://blogs.sap.com/2013/07/22/abap-news-for-release-740/\">ABAP Language News for Release 7.4</a>. </p>","timeToRead":1,"excerpt":"In this session we have talks lined up from Mike Doyle and Jason Lante.  Huge thanks to Chris Rae and the good folks at D'alRae Solutions…","frontmatter":{"title":"September 2018","cover":"/images/Brisbane-Logo-Wheel-City.jpg","date":"2018-09-05","category":"tech","tags":["ABAP","SAP Cloud Platform","iOS","UI5"],"author":"katan"},"fields":{"slug":"/september-2018"}},"prev":{"excerpt":"In this session we have talks lined up from Jakob Kjaer, Colleen Hebbert (SAP Mentor) and Surya. We'll be…","frontmatter":{"title":"November 2018","cover":"/images/Brisbane-Logo-Wheel-City.jpg","date":"2018-11-15"},"fields":{"slug":"/november-2018"}},"next":{"excerpt":"In this session we plan to have three speakers discuss the topics from SAPUI5, SAP Build and the SAP Business…","frontmatter":{"title":"May 2018","cover":"/images/Brisbane-Logo-Wheel-City.jpg","date":"2018-05-31"},"fields":{"slug":"/may-2018"}},"authors":{"edges":[{"node":{"id":"jakob","name":"Jakob Kjaer","image":"https://pbs.twimg.com/profile_images/664612830595813376/gQBGR35i_bigger.jpg","url":"https://sitbne.github.io/","bio":"- Family man - Eternal learner - Design thinking evangelist - Relentless optimist - Developer in disguise"}},{"node":{"id":"jorg","name":"Jorg Thuijls","image":"https://pbs.twimg.com/profile_images/818736249842192385/sfLdwEYb_bigger.jpg","url":"https://jorg.thuijls.net/","bio":"Developing SAP, front ends and back ends."}},{"node":{"id":"katan","name":"Katan Patel","image":"https://pbs.twimg.com/profile_images/930008567741947905/AlyYDAjg_bigger.jpg","url":"https://katansapdevelop.github.io/","bio":"SAP Developer & $crypto trader. Loves Coding, cooking, gaming, football, electronic music, MTB & fishing"}},{"node":{"id":"sitbne","name":"sitbne","image":"https://pbs.twimg.com/profile_images/930008567741947905/AlyYDAjg_bigger.jpg","url":"https://sitbne.github.io/","bio":"Yeah, I like animals better than people sometimes... Especially dogs. Dogs are the best. Every time you come home, they act like they haven't seen you in a year. And the good thing about dogs... is they got different dogs for different people."}}]}},"pathContext":{"slug":"/september-2018","total":5,"prev":"/november-2018","next":"/may-2018"}}

